<template>
	<div class="addEdit-block">
		<el-form
			class="add-update-preview"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="180px"
		>
			<template >
				<el-form-item class="input" v-if="type!='info'"  label="名称" prop="name" >
					<el-input v-model="ruleForm.name" placeholder="名称" clearable  :readonly="ro.name"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="名称" prop="name" >
					<el-input v-model="ruleForm.name" placeholder="名称" readonly></el-input>
				</el-form-item>
				<el-form-item class="select" v-if="type!='info'"  label="券类型" prop="type" >
					<el-select :disabled="ro.type" v-model="ruleForm.type" placeholder="请选择券类型" >
						<el-option
							v-for="(item,index) in typeOptions"
							v-bind:key="index"
							:label="item"
							:value="item">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item v-else class="input" label="券类型" prop="type" >
					<el-input v-model="ruleForm.type"
						placeholder="券类型" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="满额" prop="fullamount" >
					<el-input-number v-model="ruleForm.fullamount" placeholder="满额" :disabled="ro.fullamount"></el-input-number>
				</el-form-item>
				<el-form-item v-else class="input" label="满额" prop="fullamount" >
					<el-input v-model="ruleForm.fullamount" placeholder="满额" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="优惠额" prop="discountamount" >
					<el-input-number v-model="ruleForm.discountamount" placeholder="优惠额" :disabled="ro.discountamount"></el-input-number>
				</el-form-item>
				<el-form-item v-else class="input" label="优惠额" prop="discountamount" >
					<el-input v-model="ruleForm.discountamount" placeholder="优惠额" readonly></el-input>
				</el-form-item>
				<el-form-item class="date" v-if="type!='info'" label="生效时间" prop="startime" >
					<el-date-picker
						value-format="yyyy-MM-dd HH:mm:ss"
						v-model="ruleForm.startime" 
						type="datetime"
						:readonly="ro.startime"
						placeholder="生效时间"
					></el-date-picker>
				</el-form-item>
				<el-form-item class="input" v-else-if="ruleForm.startime" label="生效时间" prop="startime" >
					<el-input v-model="ruleForm.startime" placeholder="生效时间" readonly></el-input>
